<template lang="html">
  <div v-show="show" class="v-dialog">
    <div @click="close" class="scrim"></div>
    <div class="v-modal" :class="{'v-center': alert}">
      <slot name="header">
        <header>
          {{title || 'Title'}}
          <i v-show="!alert" @click="close" class="v-close"></i>
        </header>
      </slot>
      <div class="v-modal-body">
        <slot></slot>
      </div>
      <slot name="footer">
        <footer :class="{'v-center': alert}">
          <button v-show="!alert" type="button" @click="close" class="flat-btn light">{{cancelText || 'cancel'}}</button>
          <button type="button" @click="ok" class="flat-btn light">{{okText || 'ok'}}</button>
        </footer>
      </slot>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
    }
  },
  props: ['show', 'title', 'okText', 'cancelText', 'closeBtn', 'alert'],
  computed: {},
  ready: function () {
  },
  attached: function () {},
  methods: {
    lock (lock) {
      lock ? document.body.style.overflow = 'hidden' : document.body.style.overflow = ''
    },
    close () {
      this.show = false
      this.$dispatch('cancel')
    },
    ok () {
      if (this.alert) {
        this.show = false
      }
      this.$dispatch('ok')
    }
  },
  components: {},
  watch: {
    show (is) {
      is ? this.lock(true) : this.lock()
    }
  }
}
</script>

<style lang="css" scoped>
.v-dialog {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  z-index: 12306;
}
.v-center {
  text-align: center !important;
}
.v-close {
  font-style: normal;
  float: right;
  color: #505051;
  font-weight: 700;
  cursor: default;
  display: inline-block;
  text-align: center;
}
.v-close:hover {
  background: rgba(153,153,153, 0.4);
}
.scrim {
  background: rgba(255, 255, 255, 0.5);
  width: 100%;
  height: 100%;
}
.v-dialog .v-modal {
  position: absolute;
  background: #fff;
  top: 100px;
  margin-left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 0 24px 8px rgba(0, 0, 0, 0.2);
  min-width: 208px;
  width: 50%;
}
.v-dialog.body-sroll .v-modal {
  height: 70%;
}
@media only screen and (max-width: 992px) {
  .v-dialog .v-modal {
    width: 80%;
  }
}
.v-dialog.body-sroll .v-modal header, .v-dialog.body-sroll .v-modal .v-modal-header {
  padding-top: 18px;
  padding-bottom: 6px;
}
.v-modal header, .v-modal .v-modal-header {
  padding: 24px 24px 0 24px;
  box-sizing: border-box;
  font-size: 18px;
  font-weight: bolder;
  color: #505051;
}
.v-modal .v-modal-body {
  padding: 24px;
  height: calc(100% - 97px);
  overflow: auto;
  box-sizing: border-box;
}
.v-dialog.body-sroll .v-modal footer, .v-dialog.body-sroll .v-modal .v-modal-footer {
  height: 48px;
  padding: 6px 16px;
  box-sizing: border-box;
}
.v-modal footer, .v-modal .v-modal-footer {
  padding: 0 16px;
  padding-bottom: 16px;
  text-align: right;
}
.flat-btn {
  height: 36px;
  min-width: 88px;
  padding: 0 16px;
  box-sizing: border-box;
  background: transparent;
  color: #7d7d7e;
  margin: 0;
  font-weight: 700;
  border: 0;
  border-radius: 0;
  font-size: 16px;
}

.flat-btn.light:hover {
  background: rgba(153,153,153, 0.4);
}
.flat-btn.light.disabled {
  background: rgba(153,153,153, 0.1);
}
.flat-btn.light.disabled:hover {
  background: rgba(153,153,153, 0.1);
}
.flat-btn.dark {
  background: rgba(204,204,204, 0.15);
}
.flat-btn.dark:hover {
  background: rgba(204,204,204, 0.25);
}
.flat-btn.dark.disabled {
  background: rgba(204,204,204, 0.1);
}
.flat-btn.dark.disabled:hover {
  background: rgba(204,204,204, 0.1);
}
</style>



