<template lang="html">
  <div id="toast-container" :class="position">
    <div v-for="tip in tips" track-by="$index" class="v-toast">
      {{tip}}
    </div>
  </div>
</template>

<script>
export default {
  ready () {

  },
  data () {
    return {
      tips: [],
      position: 'top'
    }
  },
  methods: {
    push (tip, position, time = 2000) {
      this.position = position
      console.log(position)
      this.tips.push(tip)
      setTimeout(() => {
        this.tips.shift()
      }, time)
    }
  }
}
</script>

<style lang="css" scoped>
.v-toast {
  background: #323232;
  height: 48px;
  font-size: 16px;
  padding: 16px 24px 12px 24px;
  color: #fff;
  text-align: left;
  margin-top: 8px;
  box-sizing: border-box;
  box-shadow: 0 0 8px 2px rgba(159, 159, 159, 0.2);
}
.v-toast:first-child {
  margin-top: 0;
}
#toast-container {
  position: fixed;
  top: 50px;
  min-width: 80px;
  left: 50%;
  width: 50%;
  transform: translateX(-50%);
  z-index: 1000;
}
#toast-container.bottom, #toast-container.top, #toast-container.left, #toast-container.right {
  width: auto;
  left: auto;
  transform: none;
  top: auto;
  right: auto;
  bottom: auto;
}
#toast-container.bottom {
  right: 15px;
  top: auto !important;
  bottom: 50px !important;
}
#toast-container.top {
  right: 15px;
  bottom: auto !important;
  top: 50px !important;
}
#toast-container.right {
  right: 15px;
  left: auto;
  top: 50px;
}
#toast-container.left {
  left: 15px;
  right: auto;
  top: 50px;
}
@media only screen and (max-width: 600px) {
  #toast-container {
    width: 100%;
    right: 0;
    top: 0;
  }
  #toast-container.top {
    top: 0;
  }
  #toast-container.bottom {
    bottom: 0;
    top: auto;
  }
  .v-toast {
    text-align: left;
  }
}
</style>



