<template lang="html">
  <div class="v-panel">
    <slot>
      {{header}}
    </slot>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
    }
  },
  computed: {},
  props: {
    header: {
      type: String
    }
  },
  ready: function () {},
  attached: function () {},
  methods: {},
  components: {}
}
</script>

<style lang="css" scoped>
.v-panel {
  height: 48px;
  font-size: 16px;
  padding: 16px;
  box-sizing: border-box;
  border: solid 1px #eee;
}
.v-panel:hover {
  background: rgba(153, 153, 153, 0.2);
}
.v-panel .icon {
  height: 40px;
  width: 40px;
  border-radius: 40px;
}
</style>



