<template lang="html">
  <div class="v-collapse-group">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: ['open'],
  name: 'AccordionGroup',
  ready () {
    this.taggle(this.open)
  },
  methods: {
    taggle (index = 1) {
      let vms = this.$children
      for (var i = 0; i < vms.length; i++) {
        if (i + 1 === index) {
          vms[i].open = true
        }
      }
    }
  },
  events: {
    Accordion: function (vm) {
      let vms = this.$children
      for (var i = 0; i < vms.length; i++) {
        if (vms[i] !== vm) {
          vms[i].open = false
        }
      }
    }
  }
}
</script>

<style lang="css">
.v-collapse-group {
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.2);
}
</style>



