<template lang="html">
  <div class="v-collapse">
    <panel @click="toggle" :header="header"></panel>
    <div v-show="open" transition="collapse" class="collapse-body">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import panel from './Panel'
export default {
  data: function () {
    return {
      open: false
    }
  },
  name: 'Accordion',
  props: {
    header: {
      type: String
    }
  },
  methods: {
    toggle: function () {
      this.open = !this.open
      this.$dispatch('Accordion', this)
    }
  },
  watch: {
    open (is) {
      is ? this.$el.classList.add('active') : this.$el.classList.remove('active')
    }
  },
  components: {panel}
}
</script>

<style lang="css" scoped>
.v-collapse .v-panel {
  border-bottom: 0;
}
.v-collapse.active .v-panel {
  border-bottom: solid 1px #eee;
}
.collapse-transition {
  transition: all .3s ease;
  padding: 16px;
}
.collapse-body {
  background: rgba(153, 153, 153, 0.1);;
}
.collapse-enter, .collapse-leave {
  height: 0;
  padding: 0;
  display: none;
}
</style>



